import React from "react";
import "./NavBar.css";

class NavBar extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            screenHeight: document.body.clientHeight,
        }
    }

    screenResize = () => {
        const tempState = {
            screenHeight: document.body.clientHeight,
        }

        this.setState (tempState);
    }

    componentDidMount() {
        window.addEventListener('resize', this.screenResize)
    }
      
    componentWillUnmount() {
        window.removeEventListener('resize', this.screenResize)
    }

    render() {
        const pageLinks = this.props.pages.map((name, index) => (
            <span
                onClick={this.props.onClickHandler.bind(this, index)}
                className={index === this.props.activePage ? "nav-item-active" : null}
                key={index}
            >
                {name}
            </span>
        ));

        return (
            <div className="nav-container" style={{width: (this.state.screenHeight + "px")}}>
                {pageLinks}
            </div>
        );
    };
}
export default NavBar;
