import React, { Component } from "react";
import "./App.css";
import "./GlobalClipAnimations.css";

import NavBar from "./components/NavBar";
import Home from "./components/Home";
import Projects from "./components/Projects";
import Resume from "./components/Resume";
import Contact from "./components/Contact";
import BlankPage from './components/BlankPage';

import Swyper from "./Swyper";

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            previousPage: -1,
            currentPage: -1,
            projects: [],
        };
        this.pages = [Home, Projects, Resume, Contact];

        this.fetchProjects = this.fetchProjects.bind(this);
    }
    
    changeViewHandler = pageNum => {
        const newState = {
            previousPage: this.state.currentPage,
            currentPage: pageNum
        };
        this.setState(newState);
    };

    scrollEvent = direction => {
        if (direction === "up") {
            if (this.state.currentPage !== 0) {
                this.changeViewHandler(this.state.currentPage - 1);
            } 
        }
        if (direction === "down") {
            if (this.state.currentPage !== this.pages.length - 1) {
                this.changeViewHandler(this.state.currentPage + 1);
            } 
        }
    }
    
    wheelHandler = e => {
        window.removeEventListener("wheel", this.wheelHandler)
        
        if (e.deltaY < 0) this.scrollEvent("up");
        else if (e.deltaY > 0) this.scrollEvent("down");
        setTimeout (()=> {
            window.addEventListener("wheel", this.wheelHandler);
        }, 1500);
    }

    swiper = new Swyper(window);
    
    componentDidMount() {
        this.fetchProjects();
        if("ontouchstart" in window){
            this.swiper.onDown(()=>this.scrollEvent("up"));
            this.swiper.onUp(()=>this.scrollEvent("down"));
            this.swiper.run();

         }
        window.addEventListener('wheel', this.wheelHandler);
    }

    fetchProjects(){
        fetch('https://gh4as89ume.execute-api.us-east-2.amazonaws.com/default/ResumeEndpoint', {
            method: 'GET',
            headers: {
                'x-api-key': 'VdKyH0qD6T8rJfd0rxqqGW7BjIpklX4qTpUAhc40',
                'Content-Type': 'application/json',
            }
        }).then(response => response.json()).then(data => {
            this.setState({
                ...this.state,
                projects: data,
                currentPage: 0,
            })
        }).catch((error) => {
            console.log(error)
            setTimeout(()=>this.fetchProjects, 1000);
        });
    }
        
    render() {
        const BackPage = (this.state.previousPage === -1) ? BlankPage : this.pages[this.state.previousPage];
        const FrontPage = (this.state.currentPage === -1) ? BlankPage : this.pages[this.state.currentPage];

        // const FrontPage = this.pages[this.state.currentPage];
        return (
            <React.Fragment>

                {(this.state.currentPage !== -1)
                    ?<NavBar
                        pages={["home", "projects", "resume", "contact"]}
                        activePage={this.state.currentPage}
                        onClickHandler={this.changeViewHandler}
                    />
                    :""
                } 

                <BackPage inFocus={false} />
                
                {(this.state.currentPage === 1)
                    ? <FrontPage inFocus={true} projects={this.state.projects}/>
                    : <FrontPage inFocus={true} />
                }
            </React.Fragment>
        );
    }
}

export default App;
